const API_BASE_URL = "https://api.tools.gavago.fr/socketio/api";

export interface ApiResponse<T> {
  success?: boolean;
  data?: T;
  error?: string;
  [key: string]: unknown;
}

export interface ImageUploadResponse {
  success: boolean;
  message?: string;
}

export interface ImageGetResponse {
  success: boolean;
  data_image?: string;
}

export async function fetchData<T>(endpoint: string): Promise<ApiResponse<T>> {
  try {
    const headers = {
      "Content-Type": "application/json",
      "Accept": "application/json",
    };

    const url = `${API_BASE_URL}${endpoint}`;

    const res = await fetch(url, {
      headers
    });

    if (!res.ok) {
      throw new Error(`Erreur HTTP! Statut: ${res.status}`);
    }

    const data = await res.json();
    return data;
  } catch (error) {
    return { error: (error as Error).message };
  }
}

export async function sendImage(socketId: string, imageData: string): Promise<ApiResponse<ImageUploadResponse>> {
  try {
    const headers = {
      "Content-Type": "application/json",
    };

    // Utiliser le proxy local pour √©viter les probl√®mes CORS
    const url = `/api/images`;

    const res = await fetch(url, {
      method: "POST",
      headers,
      body: JSON.stringify({
        id: socketId,
        image_data: imageData
      })
    });

    if (!res.ok) {
      throw new Error(`Erreur HTTP! Statut: ${res.status}`);
    }

    const data = await res.json();
    return data;
  } catch (error) {
    return { error: (error as Error).message };
  }
}

export async function getImage(imageId: string): Promise<ApiResponse<ImageGetResponse>> {
  try {
    const headers = {
      "Content-Type": "application/json",
      "Accept": "application/json",
    };

    // Utiliser le proxy local pour √©viter les probl√®mes CORS
    const url = `/api/images?id=${imageId}`;

    const res = await fetch(url, {
      headers
    });

    if (!res.ok) {
      throw new Error(`Erreur HTTP! Statut: ${res.status}`);
    }

    const data = await res.json();
    return data;
  } catch (error) {
    return { error: (error as Error).message };
  }
}
